import React from 'react'
import { Grid, Row, Col } from 'react-flexbox-grid'
import './index.css'

import aboutTextL from './aboutTextL.svg'
import process from './process.svg'

import iconEmail from './icon_email.svg'
import iconFacebook from './icon_facebook.svg'
import iconInsta from './icon_insta.svg'

const About = () => (
  <div className='About'>

    <Grid fluid>
      <Row>
        <Col>
          <div className='introHead'>
            <h4> TOTAL DESIGN SOLUTION </h4>
          </div>
          <div className='intro'>
          , , , , , ,    <br />
                 <br />
           Art and Architecture Atelier.
        </div>
        </Col>
      </Row>

      <Row center='xs'>
        <Col xs={12} sm={10} md={9} lg={6} xl={5}>
          <div className='aboutImage'>
            <img src={aboutTextL} />
          </div>
        </Col>
      </Row>
    </Grid>

    <div className='ab0'>
      <Grid fluid>
        <Row>
          <Col xs={4} sm={4} md={3} />
          <h4>  SPACE IDENTITY DESIGN PROCESS </h4>

          <Col xs={8} sm={8} md={9}>
            <div className='aboutText'>
              <img src={process} style={{height: '15rem'}} />
              <br />
                  .
            </div>
          </Col>

          <Col xs={4} sm={4} md={3}>
            <h4> SOCIAL ISSUE </h4>
          </Col>

          <Col xs={8} sm={8} md={9}>
            <div className='aboutText'>
            AAAtelier             ,     .
          </div>
          </Col>

          <Col xs={4} sm={4} md={3}>
            <h4> COMMUNITY SPACE </h4>
          </Col>

          <Col xs={8} sm={8} md={9} >
            <div className='aboutText'>
                ,  ,         .
          </div>
          </Col>

        </Row>
      </Grid>
    </div>

    <hr style={{paddingTop: '1rem'}} />
    <div className='ending'>
      <Grid fluid>
        <Row>
          <Col xs={4.1} sm={4} md={3}>
             CONTACT AAAtelier
            <p style={{height: '0.9rem', marginTop: '0.3rem'}}>
             aaatelier @ ejay.kim
           </p>
          </Col>

          <Col xs={7.9} sm={8} md={7} >
            <a href='aaatelier@ejay.kim'>
              <img src={iconEmail} className='icon' />
            </a>
            <a href='www.facebook.com/kechyap'>
              <img src={iconFacebook} className='icon' />
            </a>
            <a href='www.instagram.com/aaatelier_ejay'>
              <img src={iconInsta} className='icon' />
            </a>
          </Col>
        </Row>
      </Grid>
    </div>
  </div>
)

export default About



